export default {
	data() {
		return {
			Router:this.$Router,
			showView: false,
			score:'',
			wx_info:{},
			scrollTop: 100,
			index: 0,
			current:1,
			labelData: [
				"../../static/images/home-banner.png",
				"../../static/images/home-banner.png",
			],
			indProDate:[
				{},{},{},{}	
			],
			mainData:[],
			getBefore:{},
			category_id:57,
			user_no:''
		}
	},
	
	onLoad() {
		const self = this;
		self.paginate = self.$Utils.cloneForm(self.$AssetsConfig.paginate);	
		self.user_no = uni.getStorageSync('user_info').user_no;
		const callback = (res) =>{
			self.$Utils.loadAll(['getMainData','getLabelData'], self);	
		};
		self.$Token.getProjectToken(callback,{refreshToken:true})	
	},
	
	
	onReachBottom() {
		console.log('onReachBottom')
		const self = this;
		if (!self.isLoadAll && uni.getStorageSync('loadAllArray')) {
			self.paginate.currentPage++;
			self.getMainData()
		};
	},
	
	methods: {
		
		change(current) {
			const self = this;
			if(current!=self.current){
				self.current = current;
				if(self.current==1){
					self.getBefore = {};
					self.category_id = 57
				}else{
					self.getBefore = {
						caseData: {
							tableName: 'Log',
							searchItem: {
								type:['in',[6]],
								user_no:['in',[uni.getStorageSync('user_info').user_no]]
							},
							middleKey: 'id',
							key: 'relation_id',
							condition: 'in',
						},
					};
					self.category_id = ''
				};
				self.getMainData(true)
			}
		},
		
		getMainData(isNew) {
			const self = this;
			if (isNew) {
				self.mainData = [];
				self.paginate = {
					count: 0,
					currentPage: 1,
					is_page: true,
					pagesize: 5
				}
			};
			const postData = {};
			postData.paginate = self.$Utils.cloneForm(self.paginate);
			postData.searchItem = {
				thirdapp_id: 2,
				type:1,
				level:uni.getStorageSync('user_info').level
			};
			if(self.category_id!=''){
				postData.searchItem.category_id = self.category_id
			};
			if(JSON.stringify(self.getBefore)!='{}'){
				postData.getBefore = self.$Utils.cloneForm(self.getBefore);
			};
			const callback = (res) => {
				if (res.info.data.length > 0) {
					self.mainData.push.apply(self.mainData, res.info.data);
				}
				console.log('self.mainData', self.mainData)
				self.$Utils.finishFunc('getMainData');
			};
			self.$apis.productGet(postData, callback);
		},
		
		getLabelData() {
			const self = this;
			const postData = {};
			postData.searchItem = {
				thirdapp_id: 2,
				title:'店铺轮播'
			};
			const callback = (res) => {
				if (res.info.data.length > 0) {
					self.labelData = res.info.data[0]
				}
				self.$Utils.finishFunc('getLabelData');
			};
			self.$apis.labelGet(postData, callback);
		},
	},
};
